import React from 'react';
import { Link } from 'react-router-dom';
import { FiEye, FiClock } from 'react-icons/fi';
import { formatViews, formatDate, formatDuration } from '../../utils/helpers';

const VideoCard = ({ video, size = 'normal' }) => {
  if (!video) return null;

  const { _id, title, thumbnail, views, duration, uploadDate, category, slug } = video;

  const sizes = {
    small: {
      card: 'w-full',
      image: 'aspect-video',
      title: 'text-sm line-clamp-2',
      meta: 'text-xs',
    },
    normal: {
      card: 'w-full',
      image: 'aspect-video',
      title: 'text-base line-clamp-2',
      meta: 'text-sm',
    },
    large: {
      card: 'w-full',
      image: 'aspect-video',
      title: 'text-lg line-clamp-2',
      meta: 'text-sm',
    },
  };

  const currentSize = sizes[size] || sizes.normal;

  return (
    <Link
      to={`/watch/${_id}${slug ? `/${slug}` : ''}`}
      className={`group block ${currentSize.card}`}
    >
      <div className="card card-hover">
        {/* Thumbnail */}
        <div className={`relative ${currentSize.image} bg-gray-200 dark:bg-dark-100 overflow-hidden`}>
          <img
            src={thumbnail || '/placeholder-video.jpg'}
            alt={title}
            className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
            loading="lazy"
            onError={(e) => {
              e.target.src = 'https://via.placeholder.com/640x360?text=Video';
            }}
          />
          
          {/* Duration Badge */}
          {duration && (
            <div className="absolute bottom-2 right-2 px-2 py-1 bg-black/80 rounded text-white text-xs font-medium flex items-center gap-1">
              <FiClock className="w-3 h-3" />
              {formatDuration(duration)}
            </div>
          )}
          
          {/* Hover Overlay */}
          <div className="absolute inset-0 bg-black/0 group-hover:bg-black/20 transition-colors duration-300 flex items-center justify-center">
            <div className="w-16 h-16 rounded-full bg-primary-600/90 flex items-center justify-center opacity-0 group-hover:opacity-100 transform scale-75 group-hover:scale-100 transition-all duration-300">
              <svg className="w-8 h-8 text-white ml-1" fill="currentColor" viewBox="0 0 20 20">
                <path d="M6.3 2.841A1.5 1.5 0 004 4.11V15.89a1.5 1.5 0 002.3 1.269l9.344-5.89a1.5 1.5 0 000-2.538L6.3 2.84z" />
              </svg>
            </div>
          </div>
        </div>

        {/* Info */}
        <div className="p-3">
          <h3 className={`font-medium text-gray-900 dark:text-white group-hover:text-primary-600 dark:group-hover:text-primary-500 transition-colors ${currentSize.title}`}>
            {title}
          </h3>
          
          <div className={`mt-2 flex items-center gap-3 text-gray-500 dark:text-gray-400 ${currentSize.meta}`}>
            {/* Views */}
            <span className="flex items-center gap-1">
              <FiEye className="w-3.5 h-3.5" />
              {formatViews(views)} views
            </span>
            
            {/* Date */}
            <span>‚Ä¢</span>
            <span>{formatDate(uploadDate)}</span>
          </div>

          {/* Category */}
          {category && (
            <div className="mt-2">
              <span className="text-xs px-2 py-1 bg-gray-100 dark:bg-dark-100 text-gray-600 dark:text-gray-400 rounded-full">
                {category.name}
              </span>
            </div>
          )}
        </div>
      </div>
    </Link>
  );
};

export default VideoCard;