import React, { useEffect, useState, useRef } from 'react';
import { publicAPI } from '../../services/api';
import { getDeviceType } from '../../utils/helpers';

const AdBanner = ({ placement, className = '' }) => {
  const [ad, setAd] = useState(null);
  const [loading, setLoading] = useState(true);
  const adRef = useRef(null);
  const impressionRecorded = useRef(false);

  useEffect(() => {
    const fetchAd = async () => {
      try {
        const device = getDeviceType();
        const response = await publicAPI.getAdByPlacement(placement, device);
        if (response.data.success && response.data.ad) {
          setAd(response.data.ad);
        }
      } catch (error) {
        console.error('Failed to fetch ad:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchAd();
  }, [placement]);

  // Record impression when ad is visible
  useEffect(() => {
    if (!ad || impressionRecorded.current) return;

    const observer = new IntersectionObserver(
      (entries) => {
        if (entries[0].isIntersecting) {
          publicAPI.recordAdImpression(ad._id);
          impressionRecorded.current = true;
          observer.disconnect();
        }
      },
      { threshold: 0.5 }
    );

    if (adRef.current) {
      observer.observe(adRef.current);
    }

    return () => observer.disconnect();
  }, [ad]);

  const handleClick = () => {
    if (ad) {
      publicAPI.recordAdClick(ad._id);
    }
  };

  if (loading || !ad) {
    return null;
  }

  return (
    <div 
      ref={adRef}
      className={`ad-banner ${className}`}
      onClick={handleClick}
    >
      {ad.type === 'script' || ad.type === 'html' ? (
        <div dangerouslySetInnerHTML={{ __html: ad.code }} />
      ) : ad.type === 'image' && ad.imageUrl ? (
        <a href={ad.targetUrl} target="_blank" rel="noopener noreferrer">
          <img 
            src={ad.imageUrl} 
            alt={ad.name}
            className="max-w-full h-auto"
          />
        </a>
      ) : (
        <div dangerouslySetInnerHTML={{ __html: ad.code }} />
      )}
    </div>
  );
};

export default AdBanner;